.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);

  .popup__inner {
    position: absolute;
    left: 35%;
    right: 35%;
    top: 35%;
    bottom: 35%;
    margin: auto;
    border-radius: 5px;
    background: white;

    .top__item {
      padding-top: 10px;
      padding-left: 20px;
      font-size: 30px; }

    .paragraph__item {
      margin-top: 30px;
      padding-left: 20px;
      border-top: 1px grey solid;
      border-bottom: 1px grey solid;
      line-height: 50px; }

    .button__item {
      float: right;
      margin-top: 35px;
      margin-right: 20px;
      background-color: #ef5b4c;
      color: #fff;
      width: 80px;
      line-height: 30px;
      border-radius: 5px;
      text-align: center; } } }

@media (max-width: 1130px) {
  .popup {
    .popup__inner {
      bottom: 30%;
      .paragraph__item {
        line-height: 30px; } } } }

@media (max-width: 820px) {
  .popup {
    .popup__inner {
      bottom: 35%;
      .top__item {
        font-size: 20px; } } } }

@media (max-width: 600px) {
  .popup {
    .popup__inner {
      bottom: 38%; } } }

@media (max-width: 515px) {
  .popup {
    .popup__inner {
      bottom: 43%; } } }

@media (max-width: 425px) {
  .popup {
    .popup__inner {
      bottom: 46%; } } }

@media (max-width: 360px) {
  .popup {
    .popup__inner {
      bottom: 40%;
      left: 30%;
      right: 30%; } } }
